.banner {
  color: white;
  object-fit: cover;
  height: 448px;
}
.bannerContents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}
.bannerTitle {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 10px;
}
.bannerDescription {
  max-width: 360px;
  font-size: 0.8rem;
  line-height: 1.3rem;
  width: 45rem;
  padding-top: 1rem;
  height: 80px;
}
.bannerButton {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding: 0.5rem 2rem;
}
.bannerButton:hover {
  color: #000;
  background-color: #e6e6e6;
}
.bannerFadeBottom{
    height:7.4rem;
  background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.50), #111);
}
